
.blockItemSkills {
    width: 100%;
    margin: 0 auto;
}

.itemSkills {
    width: 100%;
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(4, 1fr);
}

.blockItems {
    background-color: silver;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-right: 1px solid;
    align-self: flex-start;
}

.blockItems .itemsSkills {
    display: flex;
    flex-direction: column;
    align-items: center
}

.blockItems .itemsSkills .icons {
    width: 50px;
    height: 50px;
    background-color: #777;
}

.descriptionSkills {
    width: 100%;
    border: 1px solid #0039b5;
    display: flex;
    justify-content: center;

}

.description {
    max-width: 215px;
    min-height: 80px;
    line-height: 20px;
    padding: 0 2px 0 2px;
    margin: 0;
    text-align: center;
    word-wrap: break-word;
}


@media (max-width: 360px) {
    .blockItemSkills {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
    }

    .blockItemSkills .blockItems {
        width: 90%;
        margin-bottom: 50px;
    }
}